import styled from "styled-components";

export const ExperienceSection = styled.section`
  bacground: ${({ theme }) => theme.body};
  color: ${({ theme }) => theme.text};
  min-width: 100vw;
  margin: 100px auto;

  @media only screen and (max-width: 550px) {
    margin: 50px auto;
  }
`;

export const ExperienceContainer = styled.div`
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;

  .history {
    width: 80%;
    margin-right: 0;
    margin-left: auto;
    // margin-bottom: 20px;
    display: flex;
    align-items: center;
    float: left;
    // justify-content: center;

    @media only screen and (max-width: 700px) {
      width: 95%;
    }

    .about {
      width: 50%;
      padding: 20px;
      float: right;

      // @media only screen and (max-width: 450px) {
      //   padding: 10px;
      // }
    }

    .icon {
      font-size: 20px;
      margin-right: 10px;
    }

    p {
      font-size: clamp(10px, 2vw, 15px);
      white-space: nowrap;
      position: relative;
      width: clamp(100px, 15vw, 150px);
      margin-right: 10px;
      display: flex;
      align-items: center;
      user-select: none;
    }

    hr {
      // position: absolute;
      // content: "";
      width: clamp(50px, 15vw, 200px);
      height: 2px;
      color: ${({ theme }) => theme.effect};
      top: 50%;
      left: -250px;

      @media only screen and (max-width: 450px) {
        display: none;
      }
    }

    h1 {
      font-size: clamp(15px, 3vw, 35px);
      // color: ${({ theme }) => theme.text}
      transition: 0.3s ease;
      display: inline-block;

      &:hover {
        color: ${({ theme }) => theme.effect};
        text-decoration: underline;
      }
    }

    h2 {
      display: block;
      font-size: clamp(10px, 2.5vw, 30px);
      opacity: 0.5;
      user-select: none;
    }
  }
`;
